import Evento from './Evento';

interface IEventosProps {
  eventos: any[];
}
const Eventos: React.FunctionComponent<IEventosProps> = ({ eventos }) => {
  return (
    <div className="row">
      {eventos.map((evento, index) => (
        <Evento key={index} evento={evento} />
      ))}
    </div>
  );
};

export default Eventos;
